<h1 id="class-application" tabindex="-1"><a class="header-anchor" href="#class-application" aria-hidden="true">#</a> Class: Application</h1>
<p>Deferred providers will for the first call of this function be prevented to register.
The provider will still be passed to the bootProvider function wich will for the first call also prevent booting
but bootProvider will set deferred to false. So on the second call of this function, it will be registered and booted</p>
